import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ViewChild } from "@angular/core";

import { App, NavController, NavParams, Platform, Content } from "ionic-angular";

import { AppConstants } from "@app/app.constants";
import { AppGlobals } from "@app/app.globals";
import { AppLocales } from "@app/app.locales";

import { MarsAuthService } from "@services/auth.service";
import { MarsInteractionService } from "@services/interaction.service";
import { MarsNavigationService } from "@services/navigation.service";

import { Backend } from "@backend/index";
import { PaginationInstance, PaginationControlsDirective } from "ngx-pagination";
import collect from "collect.js";


@Component({
    selector: "page-inventory-items",
    templateUrl: "inventory-items.html",
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class InventoryItemsPage {

    @ViewChild("content") content: Content;
    @ViewChild("cataloguePag") cataloguePagElement: PaginationControlsDirective;
    token: string;
    translations: AppTranslations;
    category: string;
    title: any;
    catalogue: Array<any>;
    inventory: Array<any>;
    paginatedInventory: Array<any>;
    inventoryItemIds: Array<any>;
    catalogueOptions: any;
    inventoryOptions: any;
    initialized: boolean;
    merchant: string;
    productsList = "catalogue";
    inventoryUpdated: boolean;
    isFirstItemAdded: boolean;
    FIRST_INVENTORY_ITEM_ADDED = "first_inventory_item_added";

    private cataloguePag: PaginationInstance = {
        id: "catalogue-pag",
        itemsPerPage: 10,
        currentPage: 1
    };

    private inventoryPag: PaginationInstance = {
        id: "inventory-pag",
        itemsPerPage: 10,
        currentPage: 1,
    };

    constructor(private platform: Platform,
        private app: App,
        private navCtrl: NavController,
        private navParams: NavParams,
        private changeDetector: ChangeDetectorRef,
        private locales: AppLocales,
        private globals: AppGlobals,
        private authService: MarsAuthService,
        private interactionService: MarsInteractionService,
        private navigationService: MarsNavigationService
    ) {
        this.navigationService = new MarsNavigationService(this.app);
        this.navigationService.setNavCtrl(this.navCtrl);
        this.translations = this.locales.load();
    }

    ionViewCanEnter() {
        return this.navigationService.authCheck(InventoryItemsPage.name, ['merchant']);
    }

    ionViewWillEnter() {
        if (!MarsAuthService.hasRole('MERCHANT')) return;
        this.configureMarketItemComponent();
        this.init();
    }

    async init() {
       /*  let spinner = this.interactionService.spinner({ content: `${this.translations.loading}...` }); */
        try {
            this.initialized = false;
            this.token = MarsAuthService.getMarsToken();
            this.merchant = this.authService.getLoggedInUser()._id;
            this.category = this.navParams.get("category");
            await this.getInventory();
            await this.getCatalogue();
            this.initialized = true;
        } catch (e) {
            
            this.interactionService.alert(this.translations.server_failure);
        } finally {
            /* spinner.dismiss(); */
            this.changeDetector.detectChanges();
        }
    }

    async checkForUpdates() {
        if (this.inventoryUpdated) {
            await this.getCatalogue();
            this.inventoryUpdated = false;
            this.changeDetector.detectChanges();
        }
    }

    configureMarketItemComponent() {
        this.inventoryOptions = { showPicture: true, showPriceInput: true, showStockInput: true, showStatus: true };
        this.catalogueOptions = { showPicture: true, showPriceInput: true, showStockInput: true, showStatus: true };
    }

    scrollTop() {
        if (this.content) this.content.scrollToTop(500);
    }

    print(product) { }

    async getInventory() {
        return new Promise(async (resolve, reject) => {
            let spinner;
            /* if (this.initialized) spinner = this.interactionService.spinner({ content: `${this.translations.loading}...` }); */
            try {
                let inventory = (await Backend.getInventoryItems({ merchant: this.merchant, category: this.category })).data;
                this.title = inventory.description;
                this.inventory = inventory.items;
                this.inventoryItemIds = this.inventory.map((item) => item.information._id);
                this.paginateInventory(this.inventory);
                resolve(inventory);
            } catch (e) {
                reject(e);
            } finally {
                if (spinner) spinner.dismiss();
                this.changeDetector.detectChanges();
            }
        });
    }

    paginateInventory(inventory) {
        this.inventoryPag.totalItems = this.inventory.length;
        this.paginatedInventory = collect(inventory).sortBy('information.name').all();
        this.paginatedInventory = collect(inventory).forPage(this.inventoryPag.currentPage, this.inventoryPag.itemsPerPage).all();
    }

    async getCatalogue() {
        return new Promise(async (resolve, reject) => {
            let spinner;
            if (this.initialized) spinner = this.interactionService.spinner({ content: `${this.translations.loading}...` });
            try {
                let catalogue = (await Backend.getMarketItems({
                    pageSize: this.cataloguePag.itemsPerPage,
                    page: this.cataloguePag.currentPage - 1,
                    xAccessToken: this.token,
                    exclude: this.inventoryItemIds,
                    category: this.category
                })).data;
                this.cataloguePag.totalItems = catalogue.totalCount;
                this.filterCatalogue(catalogue.items);
                this.scrollTop();
                resolve(catalogue);
            } catch (e) {
                reject(e);
            } finally {
                if (spinner) spinner.dismiss();
                this.changeDetector.detectChanges();
            }
        });
    }

    filterCatalogue(catalogue) {
        // Keeps only the items that are not on the inventory already
        this.inventoryItemIds = this.inventory.map((item) => item.information._id);
        this.catalogue = catalogue.filter((item) => !(this.inventoryItemIds.indexOf(item._id) > -1));
        if (this.catalogue.length == 0) { // In case the catalogue is empty
            this.cataloguePag.totalItems = this.cataloguePag.totalItems - 1; // Goes back one page
            this.checkForUpdates(); // Gets updated items
        }
    }

    requestNewItem() {
        this.navigationService.goTo('MarketItemInformationPage', { category: this.category });
    }

    view(item) {
        this.navigationService.goTo("MarketItemDetailsPage", { slug: item.information.slug });
    }

    async save(item) {
        try {
            item.loading = true;
            let saved = (await Backend.saveInventoryItem({ ownerId: this.merchant, xAccessToken: this.token, item: item })).data;
            if (this.addedToInventory(item, saved)) this.updateInventory('add', saved);
            else this.updateInventory('update', saved);
        } catch (e) {
            
            this.interactionService.alert(this.translations.server_failure);
        } finally {
            item.loading = false;
            this.changeDetector.detectChanges();
        }
    }

    async remove(item) {
        try {
            item.loading = true;
            let removed = (await Backend.removeInventoryItem({ ownerId: this.merchant, xAccessToken: this.token, id: item._id })).data;
            this.updateInventory('remove', removed);
        } catch (e) {
            
            this.interactionService.alert(this.translations.server_failure);
        } finally {
            item.loading = false;
            this.changeDetector.detectChanges();
        }
    }

    updateInventory(operation, item) {
        this.isFirstItemAdded = this.authService.getLoggedInUser().skip && !this.authService.getLoggedInUser().skip[this.FIRST_INVENTORY_ITEM_ADDED];
        switch (operation) {
            case ('add'):  // In case the item has been added to the inventory
                this.inventory.push(item);
                this.onFirstItemAdded();
                break;
            case ('update'):  // In case the item has been updated
                this.inventory = this.inventory.map((inventoryItem, index) => { if (inventoryItem._id == item._id) this.inventory[index] = item; });
                break;
            case ('remove'):  // Removes the item from the frontend
                this.inventory = this.inventory.filter((inventoryItem) => inventoryItem._id !== item._id);
                break;
        }
        // Default for all operations
        this.filterCatalogue(this.catalogue);
        this.paginateInventory(this.inventory);
        this.inventoryUpdated = true;
    }

    addedToInventory(item, saved) {
        return item && saved && (!item.parentType || item.parentType == "Catalogue") && saved.parentType == "Inventory";
    }


    isCatalogueLastPage(pagination) {
        return pagination && pagination.currentPage == Math.ceil((pagination.totalItems / pagination.itemsPerPage));
    }

    showCatalogue() {
        return this.productsList == "catalogue";
    }

    async onFirstItemAdded() {
        /* this.interactionService.customAlert("congrats confetti", `
        <section class="congrats-title">
            ${this.translations.congrats}
        </section> 
        <section class="congrats-icon"></section>
        ${this.translations.first_item_added_to_inventory}`);
        try {
            this.isFirstItemAdded = false;
            await Backend.addSkip({ xAccessToken: this.token, skip: this.FIRST_INVENTORY_ITEM_ADDED });
        } catch (e) {
            
        } finally { 
            this.changeDetector.detectChanges();
         }
        */
    }

}



